:root{
  --footerHeight: 120px;
  --footerWrapperBottomMargin: -120px;
  --navFooterBG: red;
}

body{  
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: Dosis, Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
}
.nav.nav-wrapper, #footer{
  background-color: red;
  background-color: var(--navFooterBG);
}
#wrapper{
  min-height: 100vh;
  margin-bottom: -120px;
  margin-bottom: var(--footerWrapperBottomMargin);
}

#wrapper::after, #footer{
  content: "";
  display: block;

  height: 120px;

  height: var(--footerHeight);
}

#footer{
  overflow: hidden;
}
